import React from "react";
const SearchBar = ({ onSearch }) => {
    const [query, setQuery] = React.useState("");
    const handleInputChange = (e) => {
        const newQuery = e.target.value;
        setQuery(newQuery);
        onSearch(newQuery);
    };
    return (React.createElement("div", { id: "search-bar-container", className: "p-1 mx-2 flex content-center" },
        React.createElement("div", null),
        React.createElement("input", { type: "text", id: "search-input", placeholder: "Search...", className: "p-1 rounded-lg w-full pl-4", value: query, onChange: handleInputChange })));
};
export default SearchBar;
